"use client";
import { useState } from 'react';

export default function LoginPage() {
  // Estado para armazenar os valores dos campos de input
  const [login, setLogin] = useState('');
  const [password, setPassword] = useState('');
  // Estado para exibir mensagens de erro
  const [error, setError] = useState('');

  // Função para lidar com o envio do formulário
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // Verifica se ambos os campos estão preenchidos
    if (login && password) {
      setError('');
      // Redireciona para a página inicial (home)
      window.location.href = '/home';
    } else {
      // Exibe uma mensagem de erro se os campos estiverem vazios
      setError('Por favor, preencha ambos os campos de login e senha.');
    }
  };

  return (
    <div className="flex justify-center items-center min-h-screen p-5 bg-[#f0f4f8]">
      <div className="flex w-full max-w-4xl overflow-hidden rounded-xl shadow-lg bg-white">
        {/* Lado esquerdo - Imagem (escondido em telas pequenas) */}
        <aside className="hidden md:flex flex-1 justify-center items-center p-8 bg-[#007bff]">
          <img
            src="/imgs/SBS.D.svg"
            alt="Logo Sabesp"
            className="w-auto h-auto"
          />
        </aside>

        {/* Lado direito - Formulário */}
        <main className="flex-1 flex justify-center items-center p-10 md:p-10">
          <form className="w-full max-w-sm" onSubmit={handleSubmit}>
            <h1 className="text-2xl font-bold mb-2 text-[#3c4048]">
              Entre na sua Conta
            </h1>
            <p id="subtittle" className="text-sm mb-6 text-gray-500">
              Sistema de Demandas Sabesp
            </p>

            <label htmlFor="login" className="block mb-2 font-semibold text-sm text-[#3c4048]">
              Login
            </label>
            <input
              type="text"
              id="login"
              name="login"
              value={login}
              onChange={(e) => setLogin(e.target.value)}
              placeholder="Seu Login Sabesp"
              className="w-full p-3 mb-6 bg-gray-100 border border-gray-300 rounded-md text-sm text-[#3c4048] transition-all duration-300 focus:border-[#007bff] focus:shadow-[0_0_0_3px_rgba(0,123,255,0.1)]"
            />

            <label htmlFor="password" className="block mb-2 font-semibold text-sm text-[#3c4048]">
              Senha
            </label>
            <input
              type="password"
              id="password"
              name="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Sua Senha da Aplicação"
              className="w-full p-3 mb-6 bg-gray-100 border border-gray-300 rounded-md text-sm text-[#3c4048] transition-all duration-300 focus:border-[#007bff] focus:shadow-[0_0_0_3px_rgba(0,123,255,0.1)]"
            />
            
            {error && (
              <p className="text-red-500 text-sm mb-4">{error}</p>
            )}

            <button
              type="submit"
              className="w-full py-3 px-4 bg-[#007bff] text-white font-semibold rounded-md flex items-center justify-center gap-2 transition-all duration-300 hover:bg-[#0069d9] hover:-translate-y-0.5 active:bg-[#0056b3] active:translate-y-0"
            >
              LOGIN
              <span className="material-symbols-outlined text-white text-base">login</span>
            </button>

            <a
              href="/change-password"
              className="text-center block mt-4 text-sm text-gray-500 transition-colors duration-300 hover:text-[#007bff] hover:underline"
            >
              Esqueci minha Senha
            </a>
          </form>
        </main>
      </div>
    </div>
  );
}
